.contact-container {
    display: flex;
    flex-direction: column;
    background-color: #17141d;
    height: 100vh;
    padding: 20px;
    /* margin-top: 70px; */
    justify-content: space-evenly;
    align-items: center;
    margin-top: 70px;
}

.item1 {
    /* margin-top: 70px; */
    padding: 1.5rem;
    background: #17141d;
    margin: 12px;
    width: 700px;
    border: 2px solid rgb(255, 255, 255);
    border-radius: 12px;
    height: 250px;
    box-shadow: 0rem 1px 5rem rgb(0, 0, 0);
    transition: 1s;
}

.item2 {
    /* margin-top: 70px; */
    background:#17141d;
    padding: 1.5rem;
    margin: 12px;
    width: 700px;
    border: 2px solid rgb(236, 236, 236);
    border-radius: 12px;
    height: 250px;
    transition: 1s;
}


.item1:hover{
    transform: translateY(-1rem);
    

}
.item2:hover{
    transform: translateY(-1rem);
    

}


    
