.flex-container{
  margin-top: 70px;
  display: flex;
  background-color: #17141d;
  /* border: 2px solid grey; */
  flex-direction: row;
  height: 100%;
  align-items: center;
  justify-content: space-evenly;
  /* flex-wrap: wrap; */
}

.flex-items{
      background: #2a233a;
      border: #1e273d 2px solid;
      padding: 1.5rem;
      margin: 0px;
      height: 75vh;
      text-align: center;
      width: 343px;
      border: white;
      border-radius: 16px;
      box-shadow:  -1.5rem 1rem 3rem  #000;
      transition: 0.3s;
}

.items{
    margin-top: 100px;
    text-transform: capitalize;
    font-size: 30px;
    color: #ffffff;
}

.span21{
    text-align: justify;
    color: rgb(147, 201, 201);


}

